@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Work+Sans:100,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
    box-sizing: border-box;
    font-family: 'Work Sans', sans-serif;
    font-weight:100;}

html, body {
    margin: 0;
    padding: 0;
}

body {

    background-color: #ffffff;
}

.container {
    width: 100%;
    margin: 0 auto;
}

.center {
    display: flex;
    justify-content: center;
}

.hide {
    display: none;
}

.Dropdown-control {
  position:relative;
  right:.5em;
}
#facemesh-video {
  display: none;
  width: 640px;
  height: 480px;
}
#threejsContainer{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
#threejsContainer canvas {
  width: 640px;
  height: 480px;
  object-fit: cover;
}

#progress-bar[value] {
  position: fixed;
  margin-left: 35%;
  margin-right: 35%;
  margin-top: 40%;
  text-align: center;
  width: 30%;
  height: 2em;
  -webkit-appearance: none;
}

.videosource {
  display: none;
}

.loadingMessage{
  position: fixed;
  width: 6em;
  height: 2.5em;
  margin: auto;
  z-index: 1000;
  left: 0;
  right: 0;
  bottom: 38%;
  padding: .75em;
  border-radius: 1.5em;
  opacity: 80%;
  background: rgba(255, 255, 255, 0.39);
  font-size: 5em;
  text-align: center;
}

.loadingMessage p {
padding:0em;
margin:0em;
}

